body{
  margin: 0px;
}

.maindiv{
  background-color: rgb(5, 204, 204);
  height: 100vh;
  display: grid;
  justify-content: center;
  align-items: center;
}

.div{
  width: 40vh;
  height: 65vh;
  background-color:floralwhite;
  border: solid 2px black;
  border-radius: 20px;
  box-shadow: 10px 10px 20px black;
}
h1{
  margin-top: 3vh;
  text-align: center;
  background-color: burlywood;
}
.input{
  margin-top: 5vh;
  display: flex;
  justify-content: center;
}
button{
  width: 6vh;
  height: 6vh;
  background-color: mediumorchid;
  border-radius: 100%;
  color: azure;
  margin-left: 3vh;
  border: none;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  font-size: 1.7em;
  padding: 0px 7px 5px 7px;
  box-shadow: 2px 3px 5px black;
}
button:hover{
  background-color:green;
}
input{
  margin-left: 3vh;
  width: 20vh;
  border: none;
  border-bottom: solid 3px black;
  outline: none;
}